/* css reset */
* {
	box-sizing: border-box;
}
*:before,
*:after {
	box-sizing: border-box;
}
* {
	margin: 0;
	padding: 0;
}
ul,
ol {
	list-style: none;
}
a {
	color: inherit;
	text-decoration: none;
}
img {
	max-width: 100%;
	max-height: 100%;
}
/* style */
body {
	background: #eee;
}
.globalHeader {
	margin: 20px;
}
@media (min-width: 500px) {
	.globalHeader {
		margin: 100px 0 30px;
	}
}
.searchForm {
	display: flex;
	justify-content: space-between;
}
@media (min-width: 500px) {
	.searchForm {
		max-width: 400px;
		margin-left: auto;
		margin-right: auto;
	}
}
.searchForm > input {
	width: 100%;
	margin-right: 10px;
	height: 40px;
	padding: 0 10px;
	border: 1px solid #ddd;
	box-shadow: 0 2px 5px 1px rgba(64, 60, 67, 0.16);
	border-radius: 40px;
}
.searchForm > button {
	white-space: nowrap;
	padding: 0 28px;
	border: none;
	border-radius: 60px;
	background: rgba(26, 61, 182, 0.9);
	box-shadow: 0 2px 5px 1px rgba(64, 60, 67, 0.16);
	color: white;
	font-size: 16px;
}
.globalMain {
	max-width: 900px;
	margin-left: auto;
	margin-right: auto;
}
.siteList {
	margin: 20px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

@media (min-width: 500px) {
	.siteList {
		margin-left: 0;
		margin-right: -25px;
		justify-content: flex-start;
	}
}
.siteList > li {
	margin-bottom: 10px;
}
@media (min-width: 500px) {
	.siteList > li {
		margin-right: 25px;
	}
}
.siteList .site {
	width: 160px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	background: white;
	border: 1px solid #ddd;
	box-shadow: 0 2px 5px 1px rgba(64, 60, 67, 0.16);
	border-radius: 20px;
	padding: 20px 0;
	position: relative;
	cursor: pointer;
}
.siteList .site > .logo {
	width: 64px;
	height: 64px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 64px;
	text-transform: uppercase; /*字母大写*/
}
.siteList .site > .link {
	font-size: 14px;
	margin-top: 4px;
}
.siteList .site > .close {
	position: absolute;
	right: 10px;
	top: 5px;
	display: none;
	cursor: default;
	color: rgba(26, 61, 182, 0.9);
}
.siteList .site:hover > .close {
	display: block;
	color: rgba(26, 61, 182, 0.9);
}
.siteList .addButton {
	border: 1px solid #ddd;
	background: white;
	width: 160px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding: 20px 0;
	box-shadow: 0 2px 5px 1px rgba(64, 60, 67, 0.16);
	border-radius: 20px;
}
.siteList .addButton .icon {
	width: 56px;
	height: 56px;
}
.siteList .addButton .text {
	font-size: 14px;
	margin-top: 4px;
}
.siteList .addButton .icon-wrapper {
	width: 64px;
	height: 64px;
	display: flex;
	justify-content: center;
	align-items: center;
}
